// Base API configuration
const API_BASE_URL = process.env.NEXT_PUBLIC_API_URL || 'http://localhost:8000'

export class ApiError extends Error {
  constructor(
    message: string,
    public status: number,
    public data?: any
  ) {
    super(message)
    this.name = 'ApiError'
  }
}

async function fetchApi<T>(
  endpoint: string,
  options?: RequestInit
): Promise<T> {
  const url = `${API_BASE_URL}${endpoint}`
  
  const response = await fetch(url, {
    ...options,
    headers: {
      'Content-Type': 'application/json',
      ...options?.headers,
    },
  })

  if (!response.ok) {
    const errorData = await response.json().catch(() => ({}))
    throw new ApiError(
      errorData.message || 'API request failed',
      response.status,
      errorData
    )
  }

  return response.json()
}

// Types
export interface Task {
  task_id: string
  description: string
  status: string
  created_at: string
  updated_at: string
  current_stage: string
  progress_percent: number
  plan_version?: number
  current_step?: {
    step_id: string
    step_number: number
    description: string
    status: string
    started_at?: string
  }
  total_steps: number
  completed_steps: number
  latest_logs: Array<{
    timestamp: string
    stage: string
    model: string
    content_preview: string
  }>
  agents_in_use: string[]
  tools_in_use: string[]
}

export interface TasksResponse {
  tasks: Task[]
  total: number
}

export interface Plan {
  id: string
  task_id: string
  version: number
  goal: string
  strategy?: string
  steps: PlanStep[]
  alternatives?: any[]
  status: string
  current_step?: number
  estimated_duration?: number
  actual_duration?: number
  created_at: string
  approved_at?: string
}

export interface PlanStep {
  step_id?: string
  description: string
  status?: string
  assigned_agent?: string
}

export interface Agent {
  id: string
  name: string
  description?: string
  status: string
  capabilities?: string[]
  version: number
  created_at: string
  updated_at: string
  total_tasks_executed: number
  successful_tasks: number
  failed_tasks: number
}

export interface Message {
  id: string
  task_id: string
  role: 'user' | 'assistant' | 'system'
  content: string
  created_at: string
}

// API Client
export const api = {
  // Tasks (using current-work endpoint)
  tasks: {
    list: () => fetchApi<TasksResponse>('/api/current-work/tasks').then(res => res.tasks),
    get: (id: string) => fetchApi<Task>(`/api/current-work/task/${id}`),
  },

  // Plans
  plans: {
    list: (taskId?: string) => {
      const params = taskId ? `?task_id=${taskId}` : ''
      return fetchApi<Plan[]>(`/api/plans${params}`)
    },
    get: (id: string) => fetchApi<Plan>(`/api/plans/${id}`),
    create: (taskId: string) =>
      fetchApi<Plan>('/api/plans', {
        method: 'POST',
        body: JSON.stringify({ task_id: taskId }),
      }),
  },

  // Agents
  agents: {
    list: () => fetchApi<Agent[]>('/api/agents'),
    get: (id: string) => fetchApi<Agent>(`/api/agents/${id}`),
  },

  // Messages/Chat
  chat: {
    sendMessage: (taskId: string, content: string) =>
      fetchApi<Message>('/api/chat', {
        method: 'POST',
        body: JSON.stringify({
          task_id: taskId,
          content,
        }),
      }),
    getHistory: (taskId: string) =>
      fetchApi<Message[]>(`/api/chat/${taskId}/history`),
  },
}
