'use client'

import { useEffect, useRef, useCallback } from 'react'
import { useQueryClient } from '@tanstack/react-query'
import { toast } from 'sonner'

const WS_URL = process.env.NEXT_PUBLIC_WS_URL || 'ws://localhost:8000/api/ws/events'

export interface WebSocketMessage {
  type: 'task_update' | 'plan_update' | 'agent_status' | 'workflow_event'
  data: any
  timestamp: string
}

export function useWebSocket(enabled = true) {
  const wsRef = useRef<WebSocket | null>(null)
  const reconnectTimeoutRef = useRef<NodeJS.Timeout>()
  const reconnectAttemptsRef = useRef(0)
  const queryClient = useQueryClient()

  const handleMessage = useCallback(
    (message: WebSocketMessage) => {
      switch (message.type) {
        case 'task_update':
          queryClient.invalidateQueries({ queryKey: ['tasks'] })
          if (message.data?.id) {
            queryClient.invalidateQueries({
              queryKey: ['tasks', message.data.id],
            })
          }
          break

        case 'plan_update':
          queryClient.invalidateQueries({ queryKey: ['plans'] })
          if (message.data?.id) {
            queryClient.invalidateQueries({
              queryKey: ['plans', message.data.id],
            })
          }
          break

        case 'agent_status':
          queryClient.invalidateQueries({ queryKey: ['agents'] })
          break

        case 'workflow_event':
          // Handle workflow events (for visualization)
          console.log('Workflow event:', message.data)
          break

        default:
          console.log('Unknown message type:', message.type)
      }
    },
    [queryClient]
  )

  const connect = useCallback(() => {
    if (!enabled) return

    // Stop trying after too many failed attempts
    if (reconnectAttemptsRef.current >= 10) {
      return
    }

    try {
      const ws = new WebSocket(WS_URL)
      wsRef.current = ws
      
      // Suppress browser console errors by handling them silently
      ws.addEventListener('error', () => {
        // Silently handle - browser will show error but we don't need to log it
      }, { once: true })

      ws.onopen = () => {
        console.log('WebSocket connected')
        reconnectAttemptsRef.current = 0
        // Only show toast on first successful connection
        toast.success('Connected to real-time updates')
      }

      ws.onmessage = (event) => {
        try {
          const message: WebSocketMessage = JSON.parse(event.data)
          handleMessage(message)
        } catch (error) {
          console.error('Failed to parse WebSocket message:', error)
        }
      }

      ws.onerror = (error) => {
        // Silently handle errors - backend might not be running
        // Only log in development
        if (process.env.NODE_ENV === 'development') {
          console.debug('WebSocket connection error (backend may not be running):', error)
        }
        reconnectAttemptsRef.current++
      }

      ws.onclose = (event) => {
        // Don't log if it's a normal closure or if backend is not available
        if (event.code !== 1000 && reconnectAttemptsRef.current < 3) {
          console.debug('WebSocket disconnected, will retry...')
        }
        
        // Attempt to reconnect after 5 seconds (only if enabled)
        if (enabled && reconnectAttemptsRef.current < 10) {
          reconnectTimeoutRef.current = setTimeout(() => {
            connect()
          }, 5000)
        }
      }
    } catch (error) {
      // Silently handle connection errors
      reconnectAttemptsRef.current++
      if (process.env.NODE_ENV === 'development') {
        console.debug('WebSocket connection failed (backend may not be running)')
      }
    }
  }, [enabled, handleMessage])

  const disconnect = useCallback(() => {
    if (reconnectTimeoutRef.current) {
      clearTimeout(reconnectTimeoutRef.current)
    }
    if (wsRef.current) {
      wsRef.current.close()
      wsRef.current = null
    }
  }, [])

  const send = useCallback((message: any) => {
    if (wsRef.current?.readyState === WebSocket.OPEN) {
      wsRef.current.send(JSON.stringify(message))
    } else {
      console.warn('WebSocket is not connected')
    }
  }, [])

  useEffect(() => {
    connect()
    return () => {
      disconnect()
    }
  }, [connect, disconnect])

  return {
    send,
    disconnect,
    reconnect: connect,
    isConnected: wsRef.current?.readyState === WebSocket.OPEN,
  }
}
